@import '../../styles/variables';

.background {
  position: relative;
  width: 100%;
  height: 100%;
  background: $mainColor;
  overflow: hidden;

  &__item {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100px;
    background: url('../../images/wave.png');
    background-size: 1000px 100px;

    &--1 {
      animation: wave 30s linear infinite;
      z-index: 1000;
      opacity: 1;
      animation-delay: 0s;
      bottom: 0;
    }

    &--2 {
      animation: wave2 15s linear infinite;
      z-index: 999;
      opacity: 0.5;
      animation-delay: -5s;
      bottom: 10px;
    }

    &--3 {
      animation: wave 30s linear infinite;
      z-index: 998;
      opacity: 0.2;
      animation-delay: -2s;
      bottom: 15px;
    }

    &--4 {
      animation: wave2 5s linear infinite;
      z-index: 997;
      opacity: 0.7;
      animation-delay: -5s;
      bottom: 20px;
    }
  }
}

@keyframes wave {
  0%{
    background-position-x: 0px;
  }
  100%{
    background-position-x: 1000px;
  }
}
@keyframes wave2 {
  0%{
    background-position-x: 0px;
  }
  100%{
    background-position-x: -1000px;
  }
}
